<template>
  <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" router @select="handleSelect">
    <el-menu-item index="/zksdata">zkData</el-menu-item>
    <el-menu-item index="/zksync">zksync</el-menu-item>
    <el-menu-item index="/layerzero">layerzero</el-menu-item>
    <el-menu-item index="/starknet">starknet</el-menu-item>
    <el-menu-item index="/tools">tools</el-menu-item>
    <el-menu-item index="/donate">donate</el-menu-item>
    <el-menu-item>
      <a href="https://twitter.com/0xBitDog" target="_blank"><img src="../../assets/twitter.svg" class="twitter" alt="" /></a>
      <a href="https://github.com/superWifi/myWallet" target="_blank"><img src="../../assets/github.svg" class="github" alt="" /></a>
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      activeIndex: '/'
    };
  },
  mounted() {
    let path = this.$route.path;
    let tools = ["/random", "/wallet", "/balance"];
    if (tools.indexOf(path) >= 0) {
      path = "/tools";
    }
    this.activeIndex = path || '/';
  },
  methods: {
    handleSelect(key, keyPath) {
      // console.log(key, keyPath);
    }
  }
}
</script>

<style scoped lang="stylus">
.el-menu
  text-align center
  .el-menu-item
    display inline-block
    float none

.twitter
  width 20px
  padding-bottom 5px

.github
  width 20px
  padding-bottom 5px
  margin-left 10px

</style>



