<template>
  <el-container>
    <el-header>
      <my-header />
    </el-header>
    <router-view />
    <el-dialog title="0xBitDogÈù¢Êùøüëª" :visible.sync="dialog1" width="30%" class="dialog-main">
      <span>Êï∞ÊçÆ‰ªÖ‰æõÂèÇËÄÉÔºåÂêéÁª≠Êõ¥Êñ∞ËÆ°ÂàíÔºö</span>
      <ul>
        <li>Â¢ûÂä†zksync dappÁöÑ‰∫§‰∫íÂêçÂçïÊï∞Èáè</li>
        <li>starknet/layerzero‰∫§‰∫ídappËÆ∞ÂΩïÈáëÈ¢ù</li>
      </ul>
      ÁêÉÂÖ≥Ê≥®: <a href="https://twitter.com/0xBitDog" target="_blank" style="color: inherit;">@0xBitDog</a>
  <!--<div class="svg"><img src="../../assets/twitter.svg" class="svg-color" alt="" /></div>-->
    </el-dialog>
  </el-container>
</template>

<script>
import MyHeader from "../../common/Header/Header.vue";
export default {
  name: "MainPage",
  components: {
    MyHeader
  },
  data () {
    return {
      dialog1: false
    }
  },
  mounted() {
    let diag = window.localStorage.getItem("mainDialog");
    let now = Date.now();
    if (diag != undefined) {
      let diff = now - Number(diag);
      if (diff > 24*3600*1000) {
        this.dialog1 = true;
        window.localStorage.setItem("mainDialog", String(now));
      }
    } else {
      this.dialog1 = true;
      window.localStorage.setItem("mainDialog", String(now));
    }
  }
}
</script>

<style lang="stylus" scoped>
/deep/.dialog-main .el-dialog__body
  padding-top 5px

.svg
  display inline-block
  width 20px
  overflow hidden
  .svg-color
    transform translateX(20px)
    filter drop-shadow(rgb(29,155,240) -20px 0px 0px)

</style>



